@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");

@keyframes fadeIn {
	0% { opacity: 0; }
	100% { opacity: 1; }
}

@keyframes slideIn {
	0% {
		transform: translateY(20px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}

body {
	background-color: #1a1a1a;
	color: #e6e6e6;
	font-family: "Roboto", sans-serif;
}

.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 20px;
	max-width: 800px;
	margin: auto;
	animation: fadeIn 1s ease;
}

.input-container {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: 20px;
}

.input-container input {
	padding: 10px;
	border: none;
	border-radius: 5px;
	flex: 1;
	margin-right: 10px;
	transition: box-shadow 0.3s ease;
	background-color: #333;
	color: #fff;
	animation: slideIn 0.5s ease;
}

.input-container input:focus {
	box-shadow: 0px 0px 10px #007bff;
}

.button {
	padding: 10px 20px;
	border: none;
	border-radius: 5px;
	background: #007bff;
	color: white;
	cursor: pointer;
	transition: background 0.3s ease;
	animation: slideIn 0.5s ease 0.1s;
}

.button:hover {
	background: #0056b3;
}

.item-list {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
	gap: 20px;
	margin-top: 20px;
}

.item-container {
	border: 1px solid #ddd;
	border-radius: 5px;
	padding: 10px;
	transition: transform 0.3s ease;
	animation: slideIn 0.5s ease;
	background-color: #333;
}

.item-container:hover {
	transform: scale(1.02);
}

.item-image {
	width: 100%;
	height: auto;
}

.item-link {
	color: #007bff;
	text-decoration: none;
}

.status {
	display: inline-block;
	margin-top: 10px;
	padding: 5px;
	border-radius: 5px;
	color: white;
	transition: background 0.3s ease;
}

.recommended {
	background: #28a745;
}

.not-recommended {
	background: #dc3545;
}

.sold-out {
	background: #dc3545;
}

.in-stock {
	background: #28a745;
}